import { Component, OnInit } from '@angular/core';
import { Coupon } from './../../common/Coupon';
import {SharedDataService} from './../../services/shared-data.service';

@Component({
  selector: 'app-get-coupon-by-type',
  templateUrl: './get-coupon-by-type.component.html',
  styleUrls: ['./get-coupon-by-type.component.css']
})
export class GetCouponByTypeComponent implements OnInit {

  coupon : Coupon[] = [];

  constructor(private _dataService : SharedDataService) { 
    this.coupon = _dataService.coupons;
    this.getAllCoupons();
    // now the company is in the service
     
  }


  public getAllCoupons(){
    this._dataService.getAllCouponsService();   
  }
  
  public getCouponById(id:number){
    this._dataService.getCouponByIdService(id);   
  }

  public getCouponByType(type:any){
    this._dataService.getCouponByTypeService(type);   
  }

  ngOnInit() {
  }

}
