import { Injectable } from '@angular/core';

import { Coupon } from './../common/Coupon';
import { Company } from './../common/Company';
import { Http } from '@angular/http';
import Swal from 'sweetalert2';



@Injectable({
  providedIn: 'root'
})
export class SharedDataService {

  public couponAdd: Coupon = new Coupon(0, "", null, null, 0, "", "", 0, "",false);
  public couponExist: boolean;
  public purchasedcoupons: Coupon[] = [] //purchased Coupons
  public unpurchasedcoupons: Coupon[] = [] //purchased Coupons
  public coupons: Coupon[] = [] //purchased Coupons
  public companies: Company[] = []

  constructor(private http: Http) {
  }

  public logout(request, response) {
    this.http.post('http://localhost:8080/customerservice/logout', request, response).subscribe(
      (resp) => {

        Swal(
          'You are logged out',
          ' You have been logout of the System.',
          'info'
        )
      },

      //Error handeling
      (err) => {
        console.error('Error while add Coupon , Check you DB and Service!', err);
        Swal('Oops...', 'Something went wrong!', 'error')
      }
    );
  }

  /**
 * addCompanyService
 */
  public addCouponService(coupons: Coupon) {
    console.log(coupons)
    this.http.post('http://localhost:8080/companyservice/createcoupon', coupons).subscribe(
      (resp) => {
        this.getAllCouponsService();

      }
    );
  }

  public getCouponByIdService(id: any) {
    console.log(id)
    console.log('url = http://localhost:8080/companyservice/getcoupon/' + id)
    this.http.get('http://localhost:8080/companyservice/getcoupon/' + id).subscribe(
      (resp) => {
        let temp = resp.json();
        this.coupons.length = 0;

        this.coupons.push(resp.json());
      });
  }

  public getCouponByTypeService(type: any) {
    this.http.get('http://localhost:8080/companyservice/getcouponbytype/' + type).subscribe(
      (resp) => {
        let temp = resp.json();
        this.coupons.length = 0;

        for (let item of temp) {
          this.coupons.push(item);
        }
      });
  }

  public updateCouponService(couponAdd: Coupon) {
    let url = 'http://localhost:8080/customerservice/updatecoupon/' + couponAdd.id;

    this.http.put(url, couponAdd).subscribe(
      (resp) => {
        this.getAllCouponsService();

      }
    );
  }

  public purchaseCouponService(couponAdd: Coupon) {
    //Send the purchase coupon API to the Service
    let url = 'http://localhost:8080/customerservice/purchasecoupon';
    this.http.post(url, couponAdd).subscribe(
      (resp) => {
        // this.purchasedcoupons = this.purchasedcoupons;
        couponAdd.amount = couponAdd.amount - 1;
        this.updateCouponService(couponAdd);
        this.getallcurrentpurchasedcouponsService();
        // this.getAllCouponsService();
        Swal(
          'purchase Coupon was Successful !',
          ' Managed to purchase Coupon ' + couponAdd.title,
          'success'
        )
      },

      //Error handeling
      (err) => {
        if (err.status == 409) {
          console.error('Error while get Coupon By Type, Check you DB and Service!', err);
          Swal(
            {
              title: 'Cannot Purchase!',
              text: 'You already Purchased this coupon, choose another coupon.',
              imageUrl: 'https://www.salesbuzz.com/wp-content/uploads/2015/08/shutterstock_168715445.jpg',
              imageWidth: 400,
              imageHeight: 200,
              imageAlt: 'Custom image',
              animation: false,
              type: 'error'
            }
          )
        }
        else {
          console.error('Error while get Coupon By Type, Check you DB and Service!', err);
          Swal('Oops...', 'Something went wrong!', 'error')
        }
      }
    );
  }

  public removeCouponService(id: any) {
    this.http.delete('http://localhost:8080/companyservice/removecoupon/' + id).subscribe(
      (resp) => {
        this.getAllCouponsService();

      }
    );
  }

  containsCoupons(obj, list) {
    var i;
    for (i = 0; i < list.length; i++) {
      console.log("****************************" + list[i].id + "**********" + obj.id);
      if (list[i].id == obj.id) {
        console.log("*" + list[i].id);
        console.log("**" + obj.id);

        return true;
      }
    }
    return false;
  }

  public getAllCouponsService() {
    this.http.get('http://localhost:8080/customerservice/getallcoupons').subscribe(
      (resp) => {

        let temp = resp.json();
        this.coupons.length = 0;

        for (let item of temp) {
          if (this.containsCoupons(item, this.purchasedcoupons)) {
            item.purchased = true;
          }
          this.coupons.push(item);
        }
        console.log("All coupons " + this.coupons.length);

      },

      //Error handeling
      (err) => {
        console.error('Error while Filter Coupon, Check you DB and Service!', err);
        Swal('Oops...', 'Something went wrong!', 'error')
      })
      ;
  }

  public getallpurchasedcouponsService() {
    this.http.get('http://localhost:8080/customerservice/getallpurchasedcoupons').subscribe(
      (resp) => {

        if (resp.json.length == 0) {
          this.coupons.length = 0;
      }

        let temp = resp.json();
        this.coupons.length = 0;
        this.purchasedcoupons.length = 0;

        for (let item of temp) {
          this.coupons.push(item);
        }
        
        this.purchasedcoupons = this.coupons.slice(); 
        console.log("All purchasedcoupons " + this.purchasedcoupons.length);
        // this.purchasedcoupons.concat(this.coupons);
        Swal(
          {
            title: 'Filter Coupon was Successful !',
            text: ' Managed to get all purchased coupons.',
            type: 'success',
            timer: 1200
          }
        )
      },

      //Error handeling
      (err) => {
        console.error('Error while Filter Coupon, Check you DB and Service!', err);
        Swal('Oops...', 'Something went wrong!', 'error')
      });
  }

  public getallcurrentpurchasedcouponsService() {
    this.http.get('http://localhost:8080/customerservice/getallpurchasedcoupons').subscribe(
      (resp) => {
        
        let temp = resp.json();
        this.coupons.length = 0;
        this.purchasedcoupons.length = 0;

        for (let item of temp) {
          this.coupons.push(item);
        }
        this.getAllCouponsService();
        this.purchasedcoupons = this.coupons.slice(); 
        console.log("All purchasedcoupons " + this.purchasedcoupons.length);
        // this.purchasedcoupons.concat(this.coupons);
        
      },

      //Error handeling
      (err) => {
        console.error('Error while Filter Coupon, Check you DB and Service!', err);
        Swal('Oops...', 'Something went wrong!', 'error')
      });
  }

  public getallunpurchasedcouponsService() {
    this.http.get('http://localhost:8080/customerservice/getallunpurchasedcoupons').subscribe(
      (resp) => {
        if (resp.json.length == 0) {
          this.coupons.length = 0;
      }

        let temp = resp.json();
        this.coupons.length = 0;

        for (let item of temp) {
          this.coupons.push(item);
        }
        Swal(
          {
            title: 'Filter Coupon was Successful !',
            text: ' Managed to get all purchased coupons.',
            type: 'success',
            timer: 1200
          }
        )
      },

      //Error handeling
      (err) => {
        console.error('Error while Filter Coupon, Check you DB and Service!', err);
        Swal('Oops...', 'Something went wrong!', 'error')
      });
  }

  public getAllPurchasedCouponsByTypeService(type: any) {
    this.http.get('http://localhost:8080/customerservice/getallpurchasedcouponsbytype/' + type).subscribe(
      (resp) => {
        let temp = resp.json();
        this.coupons.length = 0;

        for (let item of temp) {
          this.coupons.push(item);
        }
        Swal(
          {
            title: 'Filter Coupon was Successful !',
            text: ' Managed to get Coupons by Type ' + type,
            type: 'success',
            timer: 1200
          }
        )
      },

      //Error handeling
      (err) => {
        if (err.status == 404) {
          this.coupons.length = 0;
          console.error('Error while get Coupon By Type, Check you DB and Service!', err);
          Swal(
            {
              type: 'error',
              title: 'Cannot get coupons!',
              text: 'Did not find any Purchased coupon by Type ' + type,
              animation: true
            }
          )
        }
        else {
          console.error('Error while Filter Coupon, Check you DB and Service!', err);
          Swal('Oops...', 'Something went wrong!', 'error')
        }
      }
    );
  }


  public getAllPurchasedCouponsByPriceService(price: any) {
    console.log('url = http://localhost:8080/customerservice/getAllPurchasedCouponsByPrice/' + price)
    this.http.get('http://localhost:8080/customerservice/getallpurchasedcouponsbyprice/' + price).subscribe(
      (resp) => {
        let temp = resp.json();
        console.log(this.coupons.length);
        this.coupons.length = 0;

        console.log(this.coupons.length);
        for (let item of temp) {
          this.coupons.push(item);
        }
        Swal(
          {
            title: 'Filter Coupon was Successful !',
            text: ' Managed to get Coupons by Price ' + price,
            type: 'success',
            timer: 1200
          }
        )
      },

      //Error handeling
      (err) => {
        if (err.status == 404) {
          this.coupons.length = 0;
          console.error('Error while get Coupon By Type, Check you DB and Service!', err);
          Swal(
            {
              type: 'error',
              title: 'Cannot get coupons!',
              text: 'Did not find any Purchased coupon by Price ' + price,
              animation: true
            }
          )
        }
        else {
          console.error('Error while Filter Coupon, Check you DB and Service!', err);
          Swal('Oops...', 'Something went wrong!', 'error')
        }
      }
    );
  }

  ngOnInit() {
  }

}